// miniPlayer
<template>
  <div class="volume-control">
    <i class="iconfont icon-zuidayinliang" @click="mute" ref="volumeIcon"></i>
    <div class="progress-bar" ref="progressBar">
      <div class="track"></div>
      <!-- slider -->
      <div class="track-left" ref="trackLeft"></div>
      <div class="slider" ref="slider"></div>
      <!-- 
        , js,
          offsetX,
        , . track-leftslider
          ,, offsetX
        ,, offsetX
          
        .,sliderpercent0;
          ,slidermask,
          , ,  
       -->
      <div class="mask" ref="mask"></div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, watch, inject } from "vue"
export default {
  setup() {
    // . sliderX,.
    let percent = ref(0)
    const slider = ref(null)
    const trackLeft = ref(null)
    const mask = ref(null)
    const progressBar = ref(null)
    onMounted(() => {
      mask.value.onmousedown = () => {
        mask.value.onmousemove = computePercent
        // slider, computePercent
        computePercent()
      }
      mask.value.onmouseup = () => {
        mask.value.onmousemove = null
      }
      /*
        mask, mask, mask
          onmouseup,onmousemove
        onmouseleave, 
      */
      mask.value.onmouseleave = () => {
        setTimeout(() => {
          mask.value.onmousemove = null
        }, 200)
      }
      // slider
      percent.value = 70
    })

    // percent
    function computePercent() {
      const totalWidth = progressBar.value.clientWidth
      let mouseX = window.event.offsetX
      // mask5px,
      percent.value = ((mouseX - 5) / totalWidth) * 100
      // percent0100
      if (percent.value < 0) {
        percent.value = 0
      } else if (percent.value > 100) {
        percent.value = 100
      }
    }

    // percent, slider,
    const volumeIcon = ref(null)
    const setVolume = inject("setVolume")
    watch(percent, (newValue) => {
      // 
      moveSlider(newValue)
      // 
      setVolume(newValue)
      // 
      switch (true) {
        case newValue === 0:
          changeIconTo("icon-guanbishengyin")
          break
        case newValue <= 33:
          changeIconTo("icon-yinliang")
          break
        case newValue > 33 && newValue <= 66:
          changeIconTo("icon-zhongdengyinliang")
          break
        case newValue > 66:
          changeIconTo("icon-zuidayinliang")
          break
      }
    })
    function changeIconTo(iconName) {
      volumeIcon.value.classList.remove(
        "icon-guanbishengyin",
        "icon-zhongdengyinliang",
        "icon-zuidayinliang",
        "icon-yinliang"
      )
      volumeIcon.value.classList.add(iconName)
    }

    // percentslider
    function moveSlider(percent) {
      const totalWidth = progressBar.value.clientWidth
      // slider,. slider,slider
      const correction = (slider.value.clientWidth / 2 / totalWidth) * 100
      let position = percent - correction
      slider.value.style.left = position + "%"
      // position,width,0
      trackLeft.value.style.width = position < 0 ? "0%" : position + "%"
    }

    let previousPercent = 0
    function mute() {
      if (percent.value !== 0) {
        previousPercent = percent.value
        percent.value = 0
      } else {
        percent.value = previousPercent
      }
    }

    return {
      slider,
      trackLeft,
      mask,
      progressBar,
      mute,
      volumeIcon,
    }
  },
}
</script>

<style lang="less" scoped>
.volume-control {
  display: flex;
  align-items: center;
  margin-right: 22px;
  i {
    margin-right: 8px;
    font-size: @font-size-title;
    cursor: pointer;
  }

  .progress-bar {
    display: flex;
    align-items: center;
    position: relative;
    width: 120px;
    height: 30px;
    .track {
      width: 100%;
      height: 2px;
      background-color: var(--progress-bgcolor);
    }
    .track-left {
      position: absolute;
      left: 0;
      width: 50px;
      height: 2px;
      background-color: @theme-color;
    }
    .slider {
      position: absolute;
      left: 20px;
      width: 12px;
      height: $width;
      border-radius: 50%;
      background-color: @theme-color;
    }
    .mask {
      position: absolute;
      top: -10px;
      bottom: -10px;
      left: -5px;
      right: -10px;
    }
  }
}
</style>
