// header, 
<template>
  <div class="theme" ref="themeEle">
    <span class="icon-wrapper" @click="showOptionsFn"><i class="iconfont icon-pifu"></i></span>
    <div class="options" v-show="showOptions">
      <div
        class="option"
        v-for="(themeValue, themeKey, index) in themes"
        :key="index"
        @click="changeTheme(themeValue)"
      >
        <div class="icon" :style="themeValue.style"></div>
        <p>{{ themeValue.title }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import variablesWhite from "@/style/themeColors/variables-white"
import variablesRed from "@/style/themeColors/variables-red"
import variablesDark from "@/style/themeColors/variables-dark"
import { ref, onBeforeMount } from "vue"
export default {
  setup() {
    let showOptions = ref(false)
    let themes = {
      white: {
        title: "",
        file: variablesWhite,
        style: {
          backgroundColor: "#F6F6F6",
          border: "1px solid #eee",
        },
      },
      red: {
        title: "",
        file: variablesRed,
        style: {
          backgroundColor: "#D33A31",
        },
      },
      dark: {
        title: "",
        file: variablesDark,
        style: {
          backgroundColor: "#202020",
        },
      },
    }
    let localTheme = JSON.parse(localStorage.getItem("theme"))
    // css
    onBeforeMount(() => {
      // ,,
      if (localTheme) {
        changeTheme(localTheme)
      } else {
        // ,
        changeTheme(themes.white)
      }
    })
    // css,(html)
    function changeTheme(theme) {
      localStorage.setItem("theme", JSON.stringify(theme))
      const variables = theme.file
      Object.keys(variables).forEach((key) => {
        const value = variables[key]
        document.documentElement.style.setProperty(key, value)
      })
    }

    // /
    // : ,,
    function showOptionsFn() {
      /*
       ,document,.
       , ,document
       document,..
       ,
       : ,

       : ,(event.target)
        <div class='theme'>...</div> . ,,,
      */

      // toggleshowOptions,,
      document.removeEventListener("click", toggleShowThemeOptions)
      document.addEventListener("click", toggleShowThemeOptions)
    }

    let themeEle = ref(null)
    // removeEventListener
    function toggleShowThemeOptions(event) {
      // pn, 
      let pn = event.target.parentNode
      //  pnhtml
      while (pn !== document.documentElement) {
        if (pn === themeEle.value) {
          showOptions.value = true
          return
        }
        pn = pn.parentNode
      }
      // ,theme,
      showOptions.value = false
      // ,
      document.removeEventListener("click", toggleShowThemeOptions)
    }
    return { themeEle, themes, changeTheme, showOptions, showOptionsFn }
  },
}
</script>

<style lang="less" scoped>
.theme {
  position: relative;
  display: flex;
  .icon-wrapper {
    width: 25px;
    height: $width;
    line-height: $width;
    text-align: center;
    border-radius: 50%;
    transition: 0.3s;
    &:hover {
      background-color: var(--round-hover-bgcolor);
    }
    i {
      color: var(--header-font-color);
    }
  }
  .options {
    position: absolute;
    z-index: 1000;
    top: 35px;
    left: -180px;
    display: flex;
    justify-content: space-evenly;
    width: 230px;
    padding: 13px;
    border-radius: 5px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    background-color: var(--prompt-bg-color);
    // 
    &::before {
      content: "";
      position: absolute;
      top: -8px;
      left: 184px;
      border: solid 8px;
      border-top: none;
      border-color: transparent transparent var(--prompt-bg-color) transparent;
    }
    .option {
      display: flex;
      flex-direction: column;
      cursor: pointer;
      .icon {
        width: 25px;
        height: $width;
        border-radius: 50%;
        margin-bottom: 10px;
      }
    }
  }
}
</style>
